.listTable
{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 5px;
}
label
{
    font-size: 20px;
    font-weight: 500;
    padding: 10px;
    flex: 1;
    cursor: pointer;
    /* border: 1px solid red; */
}
.ture
{
    text-decoration: line-through;
}
label:hover
{
    background-color: rgb(221, 218, 218);
    transition: 0.3s;
}
svg:hover
{
    cursor: pointer;
    fill: red;
}

.EditDelete
{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}