*
{
    margin: 0;
    padding: 0;
}
body
{
        width: 50%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-color:rgb(70, 67, 73);
}
@media screen and (max-width:1200px) 
{
    body
    {
        width: 60%;        
    }    
}
@media screen and (max-width:800px) 
{
    body
    {
        width: 80%;        
    }    
}
@media screen and (max-width:500px) 
{
    body
    {
        width: 80%;
        margin: 0 3%;
    }    
}
.container
{
    height: 90vh;
    /* margin: 0 10%; */
}