*
{
    margin: 0;
    padding: 0;
}
.todo
{
    width: 100%;
    /* border: 1px solid red; */
}
.todo h1
{
    color:rgb(182, 231, 83);
    margin-bottom: 10px;
}
.textInputContainer
{
    display:flex;
    column-gap:25px;
    margin-bottom: 15px;
}
@media screen and (max-width:500px) 
{
    .textInputContainer
    {
        /* border: 1px solid red; */
        display:block;
    }
}
.textInput
{
    flex: 1;
}
.textInput input
{
    min-width: 100%;
    padding: 0 10px;
    font-size: 20px;
    border: none;
}
input:focus,
button:focus
{
    outline: none;
}
input,
button
{
    height: 51px;
}
.btn button
{
    padding: 0 10px;
    border: none;
    margin-top: 0px;
    font-size: 21px;
    width: 150px;
    font-weight: bold;
    color: white;
    background-color:rgb(62, 53, 185);
}
@media screen and (max-width:500px) 
{
    .btn button
    {
        margin-top: 10px;
        border-radius: 20px;
        width: 100%;
    }
}
.textInputContainer button:hover
{
    background-color:rgb(54, 47, 161);
}
.todo p
{
    color:white;
    font-size: 18px;
    margin-bottom: 10px;
}

/* list */

.list
{
    max-height: 60vh;
    background-color: white;
    overflow: auto;
    /* border: 1px solid red; */
}
.list h3
{
    color: red;
    margin-bottom: 10px;
}
fieldset
{
    /* border: 1px solid red; */
    padding: 10px;
    border: none;
}
