/* clear */
.clear p
{
    /* border: 1px solid red; */
    cursor: pointer;
    position: absolute;
    right: 0;
    font-size: 16px;
    padding: 5px 10px ;
    border-radius: 10px;
    background-color:rgba(0, 0, 0, 0.1);
}